.App {
  display: flex;
  flex-direction: column;
  padding: 0 2%;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
}

.flex-row{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.font-roboto{
  font-family: 'Roboto', sans-serif;
  padding: 0 3%;
}

.container-cards{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.container-no-result{
  background: rgb(245,245,245);
  height: 540px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 3%;
  text-align: center;
}
